*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
#HeaderAgendamento {
    width: 100%;
    height: 170px; 
    display: flex;
    justify-content: space-evenly;
    box-shadow: 2px 5px 8px 3px rgba(0, 0, 0, 0.719);
}
#HeaderAgendamento img {
    width: 150px;
    height: 150px;
    margin-top: 10px;
    margin-left: 10px;
}
h1 {
    font-size: 20px;
    margin-top: 50px;
    font-weight: 600;
}
.h2 {
    margin-bottom: 10px;
}
#Phora {
    margin-top: 20px;
}
#DivAgendamento {
    text-align: center;
    margin-top: 20px;
    width: 80%;
    margin-left: 10%;
}
#DivCalendario {    
    width: 80%;
    margin-left: 10%;
    height: 35px;
    border: solid black 1px;
}
.Calendario {
    background-color: #5e5e74;
    color: white;
    border-radius: 5px;
    width: 60%;
    height: 33px;
}

#PHorariosDisponiveis {
    font-size: 15px;
    margin-top: 20px;
    margin-bottom: 20px;
}
#Horarios ul {
    width: 100%;
    margin-top: 15px;
}
#Horarios ul li {
    display: flex;
    justify-content: space-around;
    list-style: none;
}
#Horarios ul li p {
    font-size: 20px;
    font-weight: 600;
}
#Horarios input {
    border: solid black 1px;
    width: 100px;
    cursor: pointer;
}
#Horarios input:hover {
    opacity: 80%;
}
#Servi√ßos {
    width: 80%;
    margin-left: 10%;
}
#Servi√ßos ul {
    width: 20%;
    margin-top: 15px;
    width: 100%;
}
#Servi√ßos ul li {
    display: flex;
    flex-direction: column;
}

#Servi√ßos ul li .TipoServi√ßo {
    font-size: 20px;
    font-weight: 600;
}
#Servi√ßos ul li input {
    width: 80%;
    margin-left: 10%;
    height: 30px;
    margin-bottom: 10px;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    background-color: #343351;
}
#Servi√ßos ul li  input:hover{
    opacity: 80%;
}
#DivFormCliente {
    text-align: center;
}
#DivFormCliente form .PFormCliente {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 10px;
}
#DivFormCliente form .InputFormCliente {
    border: solid black 1px;
    width: 60%;
    height: 35px;
    margin-bottom: 10px;
}
#BtnFormCliente {
    border:  solid black 1px;
    width: 50%;
    height: 40px;
    margin-bottom: 20px;
    cursor: pointer;
}
#BtnFormCliente:hover {
    opacity: 80%;
}
